'use strict';

// MODULES //

import hasToStringTag from '@stdlib/assert-has-tostringtag-support';
import nativeClass from '@stdlib/utils-native-class';
import test from './try2getday.js';


// VARIABLES //

var FLG = hasToStringTag();


// MAIN //

/**
* Tests if a value is a `Date` object.
*
* @param {*} value - value to test
* @returns {boolean} boolean indicating if a value is a `Date` object
*
* @example
* var bool = isDateObject( new Date() );
* // returns true
*
* @example
* var bool = isDateObject( '2017-01-01' );
* // returns false
*/
function isDateObject( value ) {
	if ( typeof value === 'object' ) {
		if ( value instanceof Date ) {
			return true;
		}
		if ( FLG ) {
			return test( value );
		}
		return ( nativeClass( value ) === '[object Date]' );
	}
	return false;
}


// EXPORTS //

export default isDateObject;
